<template>
  <div class="mescroll order-scroll-height" id="mescroll" ref="div">
    <div class="whole-wrap">
      <div
        v-title
        :data-title="$store.state.order_title"
        v-if="$store.state.order_title"
      >{{$store.state.order_title}}</div>
      <div class="order-wrap" ref="order_wrap">
        <div class="order-list" v-for="(item,index) in $store.state.order" :key="item.id">
          <div class="hd clearfix">
            <span class="title fl">ShopsN</span>
            <span class="status fr" v-if="item.order_status == 0">待付款</span>
            <span class="status fr" v-if="item.order_status == 1">待发货</span>
            <span class="status fr" v-if="item.order_status == 3">待收货</span>
            <span class="status fr" v-if="item.order_status == 4 && item.order_state != '1'">已完成</span>
            <span class="status fr" v-if="item.order_status == 5 ">售后中</span>
            <span class="status fr" v-if="item.order_status == 6 ">审核失败</span>
            <span class="status fr" v-if="item.order_status == 7 ">审核通过</span>
            <span class="status fr" v-if="item.order_status == 8 ">退款中</span>
            <span class="status fr" v-if="item.order_status == 9 ">售后完成</span>
            <span class="status fr" v-if="item.order_state == '1'">待评论</span>
            <span class="status fr" v-if="item.order_status == -1">已取消</span>
          </div>
          <div class="describe clearfix" v-for="text in item.goods" :key="text.id">
            <img :src="URL + text.selfImg" class="fl" @click="toDetails(item,text,index)" />
            <div class="fl words" @click="toDetails(item,text,index)">
              <p class="text">{{text.title}}</p>
              <p class="clearfix company">
                <span class="fl" v-if="text.selfAttr[0]">{{text.selfAttr[0].name}}&nbsp;:&nbsp;</span>
                <span class="fl" v-if="text.selfAttr[0]">{{text.selfAttr[0].item}}</span>
              </p>
            </div>
            <button
              class="btn"
              @click="toRouter('/evaluate',item,text)"
              v-if="item.order_state == '1'"
            >马上评论</button>
          </div>
          <div class="commod clearfix">
            <span class="fl total">共{{item.count}}件商品</span>
            <span class="fl actual">
              实付款&nbsp;:&nbsp;
              <span>￥{{item.price_sum}}</span>
            </span>
            <span
              class="icon"
              @click="del(item,index)"
              v-if="item.order_status == 4 || item.order_status == -1"
            ></span>
          </div>
          <div class="operation clearfix">
            <span
              class="timer fl"
            >下单时间：{{new Date(item.create_time * 1000).getFullYear()+'-'+(new Date(item.create_time * 1000).getMonth()+1)+'-'+new Date(item.create_time * 1000).getDate()+' '+new Date(item.create_time * 1000).getHours()+':'+new Date(item.create_time * 1000).getMinutes()+':'+new Date(item.create_time * 1000).getSeconds()}}</span>
            <button
              class="btn fr"
              @click="toRouter('/pay',item,false)"
              v-if="item.order_status == 0"
            >马上付款</button>
            <button class="btn fr" @click="toCancel(item,'-2')" v-if="item.order_status == -1">删除订单</button>
            <button class="btn fr" @click="toCancel(item,'-1')" v-if="item.order_status == 0">取消订单</button>

            <button
              class="btn fr"
              @click="toRouter('/order',item,false)"
              v-if="item.order_status == 1"
            >查看订单</button>
            <button
              class="btn fr"
              @click="toRouter('/logis',item,false)"
              v-if="item.order_status == 3"
            >查看物流</button>
            <button class="btn fr" @click="toConfirm(item)" v-if="item.order_status == 3">确认收货</button>
            <!-- <button class="btn fr" @click="toRouter('/Cart',item,1)" v-if="item.order_status == 4 || item.order_status == -1" v-show="item.order_state != '1'">再次购买</button> -->
          </div>
        </div>
        <div v-if="$store.state.loading_status" class="text-center statu">{{$store.state.page_text}}</div>
        <div class="comm-null" v-if="!$store.state.order">
          <div class="con-wrap text-center">
            <img src="../../assets/null_com.png" />
            <p>暂时没有商品</p>
          </div>
        </div>
      </div>
      <Shopsn></Shopsn>
      <div class="load" v-show="$store.state.order_load" @touchmove.prevent>
        <mt-spinner type="triple-bounce" color="rgb(38, 162, 255)"></mt-spinner>
      </div>
      <div class="load-wrap" v-show="$store.state.order_load_wrap" @touchmove.prevent>
        <mt-spinner type="triple-bounce" color="rgb(38, 162, 255)"></mt-spinner>
      </div>
    </div>
  </div>
</template>

<script>
import { MessageBox, Toast } from "mint-ui";
import Shopsn from "@/components/page/Shopsn.vue";
import qs from "qs";
export default {
  name: "wholeOrder",
  data() {
    return {
      search: true,
      scrollWatch: true,
      order_status: "",
      page: 1,
      hide: false,
      position: false //保存的位置只赋一次值
    };
  },
  components: {
    Shopsn
  },
  updated() {
    this.$nextTick(function() {
      this.position = this.$store.state.changeHomeScrollY ? false : true;
      if (this.position === true) return;
      this.$refs.div.scrollTop = this.$store.state.changeHomeScrollY;
    });
  },
  mounted() {
    //创建MeScroll对象
    var self = this;
    self.mescroll = new MeScroll("mescroll", {
      //下拉刷新
      down: {
        callback: self.downCallback, //下拉刷新的回调,别写成downCallback(),多了括号就自动执行方法了
        use: self.down, //是否初始化下拉刷新; 默认true
        auto: false, //是否在初始化完毕之后自动执行下拉回调callback; 默认true
        autoShowLoading: false, //如果在初始化完毕之后自动执行下拉回调,是否显示下拉刷新进度; 默认false
        isLock: false, //是否锁定下拉,默认false;
        isBoth: false, //下拉刷新时,如果滑动到列表底部是否可以同时触发上拉加载;默认false,两者不可同时触发;
        offset: 60, //触发刷新的距离,默认80
        outOffsetRate: 0.2, //超过指定距离范围外时,改变下拉区域高度比例;值小于1且越接近0,越往下拉高度变化越小;
        bottomOffset: 20, //当手指touchmove位置在距离body底部20px范围内的时候结束上拉刷新,避免Webview嵌套导致touchend事件不执行
        minAngle: 45, //向下滑动最少偏移的角度,取值区间  [0,90];默认45度,即向下滑动的角度大于45度则触发下拉;而小于45度,将不触发下拉,避免与左右滑动的轮播等组件冲突;
        hardwareClass: "mescroll-hardware", //硬件加速样式;解决iOS下拉因隐藏进度条而闪屏的问题,参见mescroll.css
        warpId: null, //可配置下拉刷新的布局添加到指定id的div;默认不配置,默认添加到mescrollId
        warpClass: "mescroll-downwarp", //容器,装载布局内容,参见mescroll.css
        resetClass: "mescroll-downwarp-reset" //高度重置的动画,参见mescroll.css
      }
    });
    // this.$store.state.rollingNode = this.$refs.div.scrollTop;
    if (this.$store.state.listData.length > 0) {
      this.$store.state.order = this.$store.state.listData;
      if (this.$store.state.listInfo) {
        let listInfo = this.$store.state.listInfo;
        this.$store.state.status = listInfo.order_status;
      }
    } else {
      this.$store.state.loading_status = false;
      this.$store.commit("page_text", this.$store.state.page_text); //默认加载更多
      this.$store.commit("clear_page", this.$store.state.page); //初始化page
      switch (Number(this.$route.params.status)) {
        case 0:
          this.$store.state.order_title = "全部订单";
          this.$store.state.order_status = "";
          this.axios({
            url: API_URL + "Home/Order/myOrder",
            method: "get",
            params: {
              app_user_id: sessionStorage.getItem("user_ID"),
              order_status: this.$store.state.order_status,
              p: this.$store.state.page
            }
          })
            .then(res => {
              this.$store.state.order = res.data.data;
              this.$store.state.order_load_wrap = false;
            })
            .catch(err => {
              console.log(err);
            });
          break;
        case 1:
          this.$store.state.order_title = "待付款订单";
          this.$store.state.order_status = 0;
          this.axios({
            url: API_URL + "Home/Order/myOrder",
            method: "get",
            params: {
              app_user_id: sessionStorage.getItem("user_ID"),
              order_status: this.$store.state.order_status,
              p: this.$store.state.page
            }
          })
            .then(res => {
              this.$store.state.order = res.data.data;
              this.$store.state.order_load_wrap = false;
            })
            .catch(err => {
              console.log(err);
            });
          break;
        case 2:
          this.$store.state.order_title = "待发货订单";
          this.$store.state.order_status = 1;
          this.axios({
            url: API_URL + "Home/Order/myOrder",
            method: "get",
            params: {
              app_user_id: sessionStorage.getItem("user_ID"),
              order_status: this.$store.state.order_status,
              p: this.$store.state.page
            }
          })
            .then(res => {
              this.$store.state.order = res.data.data;
              this.$store.state.order_load_wrap = false;
            })
            .catch(err => {
              console.log(err);
            });
        case 3:
          this.$store.state.order_title = "已发货订单";
          this.$store.state.order_status = 3;
          this.axios({
            url: API_URL + "Home/Order/myOrder",
            method: "get",
            params: {
              app_user_id: sessionStorage.getItem("user_ID"),
              order_status: this.$store.state.order_status,
              p: this.$store.state.page
            }
          })
            .then(res => {
              this.$store.state.order = res.data.data;
              this.$store.state.order_load_wrap = false;
            })
            .catch(err => {
              console.log(err);
            });
          break;
        case 4:
          this.$store.state.order_title = "已完成订单";
          this.$store.state.order_status = 4;
          this.axios({
            url: API_URL + "Home/Order/myOrder",
            method: "get",
            params: {
              app_user_id: sessionStorage.getItem("user_ID"),
              order_status: this.$store.state.order_status,
              p: this.$store.state.page
            }
          })
            .then(res => {
              this.$store.state.order = res.data.data;
              this.$store.state.order_load_wrap = false;
            })
            .catch(err => {
              console.log(err);
            });
          break;
        case 5:
          this.$store.state.order_title = "已取消订单";
          this.$store.state.order_status = -1;
          this.axios({
            url: API_URL + "Home/Order/myOrder",
            method: "get",
            params: {
              app_user_id: sessionStorage.getItem("user_ID"),
              order_status: this.$store.state.order_status,
              p: this.$store.state.page
            }
          })
            .then(res => {
              this.$store.state.order = res.data.data;
              this.$store.state.order_load_wrap = false;
            })
            .catch(err => {
              console.log(err);
            });
          break;
      }
    }

    let this_ = this;
    let sw = true;
    // 注册scroll事件并监听
    this.$refs.div.addEventListener("scroll", () => {
      if (!this.$refs.order_wrap) return;
      if (this_.scrollWatch) {
        // 判断是否滚动到底部
        if (
          this_.$refs.div.scrollHeight - this_.$refs.div.clientHeight ==
          this_.$refs.div.scrollTop
        ) {
          if (this_.$store.state.no_data === true) {
            return false;
          }
          // 如果开关打开则加载数据
          if (sw == true) {
            this_.$store.state.page += 1;
            sw = false;
            if (this_.$store.state.page_text === "暂无更多数据") {
              sw = true;
              return false;
            } else {
              this.$store.state.loading_status = true;
              this.axios({
                url: API_URL + "Home/Order/myOrder",
                method: "get",
                params: {
                  app_user_id: sessionStorage.getItem("user_ID"),
                  order_status: this.$store.state.order_status,
                  p: this_.$store.state.page
                }
              })
                .then(res => {
                  sw = true;
                  this.$store.state.changeHomeScrollY = 0;
                  if (res.data.status === 0) {
                    this_.$store.state.page_text = "暂无更多数据";
                    return false;
                  }
                  res.data.data.forEach(function(val, index) {
                    this_.$store.state.order.push(val);
                  });
                })
                .catch(err => {
                  console.log(err);
                });
            }
          }
        }
      }
    });
    this.$nextTick(function() {
      this.position = this.$store.state.changeHomeScrollY ? false : true;
      if (this.position === true) return;
      this.$refs.div.scrollTop = this.$store.state.changeHomeScrollY;
    });
  },
  destroyed() {
    this.scrollWatch = false;
  },

  methods: {
    downCallback() {
      this.$store.state.changeHomeScrollY = "";
      this.$refs.div.scrollTop = 0;
      this.$store.commit("page_text", this.$store.state.page_text); //默认加载更多
      this.$store.commit("clear_page", this.$store.state.page); //初始化page
      this.axios({
        url: API_URL + "Home/Order/myOrder",
        method: "get",
        params: {
          app_user_id: sessionStorage.getItem("user_ID"),
          order_status: this.$store.state.order_status,
          p: this.$store.state.page
        }
      })
        .then(res => {
          setTimeout(() => {
            this.mescroll.endSuccess(); //无参
          }, 200);
          this.$store.state.order = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });
    },
    toDetails(item, text, index) {
      this.$router.push({
        name: "orderDetails",
        params: {
          status: item.order_status,
          order: item.id,
          order_type: 2 //1为积分订单 2 为商品订单
        }
      });
    },
    toRouter(to, item, text) {
      switch (to) {
        case "/evaluate":
          this.$store.state.order_ommodity = text;
          this.$router.push({
            name: "evaluate",
            params: {
              id: item.id
            }
          });
          break;
        case "/Cart":
          this.$router.push({
            name: "orderDetails",
            params: {
              status: 4,
              order: item.id,
              order_type: 2 //1为积分订单 2 为商品订单
            }
          });
          break;
        case "/logis":
          this.$router.push({
            name: "logis",
            params: {
              status: 3,
              id: item.id,
              order_type: 2 //1为积分订单 2 为商品订单
            }
          });
          break;
        case "/order":
          this.$router.push({
            name: "orderDetails",
            params: {
              status: 2,
              order: item.id,
              order_type: 2 //1为积分订单 2 为商品订单
            }
          });
          break;
        case "/pay":
          this.$router.push({
            name: "orderDetails",
            params: {
              status: item.order_status,
              order: item.id,
              order_type: 2 //1为积分订单 2 为商品订单
            }
          });
          break;
      }
    },
    del(item, index) {
      MessageBox.confirm("确定执行此操作?")
        .then(action => {
          this.axios
            .post(
              API_URL + "Home/Order/deleteOrder",
              qs.stringify({
                app_user_id: sessionStorage.getItem("user_ID"),
                order_id: item.id
              })
            )
            .then(res => {
              this.$store.state.order.splice(index, 1);
              Toast({
                message: res.data.msg,
                position: "bottom"
              });
            })
            .catch(err => {
              console.log(err);
            });
        })
        .catch(err => {
          console.log(err);
        });
    },
    toConfirm(item) {
      MessageBox.confirm("确定执行此操作?")
        .then(action => {
          this.axios
            .post(
              API_URL + "Home/Order/confirm_receipt",
              qs.stringify({
                order_id: item.id
              })
            )
            .then(res => {
              if (res.data.status == 1) {
                Toast({
                  message: res.data.msg,
                  position: "bottom"
                });
                this.toRouter("/");
              } else {
                Toast({
                  message: res.data.msg,
                  position: "bottom"
                });
              }
            })
            .catch(err => {
              console.log(err);
            });
        })
        .catch(err => {
          console.log(err);
        });
    },
    toCancel(item, order_status) {
      MessageBox.confirm("确定执行此操作?")
        .then(action => {
          this.axios
            .post(
              API_URL + "Home/Order/setOrderStatus",
              qs.stringify({
                app_user_id: sessionStorage.getItem("user_ID"),
                order_id: item.id,
                status: order_status
              })
            )
            .then(res => {
              if (res.data.status == 1) {
                Toast({
                  message: res.data.msg,
                  position: "bottom"
                });
                this.$store.state.page = 1;
                this.axios({
                  url: API_URL + "Home/Order/myOrder",
                  method: "get",
                  params: {
                    app_user_id: sessionStorage.getItem("user_ID"),
                    order_status: this.$store.state.order_status,
                    p: this.$store.state.page
                  }
                })
                  .then(res => {
                    this.$store.state.order = res.data.data;
                    this.$store.state.order_load_wrap = false;
                  })
                  .catch(err => {
                    console.log(err);
                  });
              } else {
                if (order_status == -1) {
                  Toast("取消失败");
                } else if (order_status == -2) {
                  Toast("删除失败");
                }
              }
            })
            .catch(err => {
              console.log(err);
            });
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>

<style lang="less" scoped>
.mescroll {
  top: 1.88rem;
  bottom: 0;
}

.whole-wrap {
  background: #f1f1f1;
  .order-wrap {
    padding-top: 0.1rem;
    .statu {
      padding: 0.2rem 0;
      font-size: 0.3rem;
      color: #757575;
    }
    .comm-null {
      padding-top: 0.5rem;
      background: #fff;
      p {
        font-size: 0.28rem;
        color: #666;
        padding-top: 0.2rem;
      }
    }
    .order-list {
      background: #fff;
      line-height: 0.7rem;
      margin-bottom: 0.2rem;
      .hd {
        margin: 0 0.2rem;
        border-bottom: 1px solid #dfdfdd;
        height: 0.7rem;
        .title {
          font-size: 0.26rem;
          color: #333;
        }
        .status {
          font-size: 0.3rem;
          color: #f9781e;
        }
        .status.light {
          color: #adadad;
        }
        .status.ash {
          color: #333;
        }
      }
      .describe {
        margin: 0 0.2rem;
        height: 1.46rem;
        padding: 0.13rem 0;
        border-bottom: 1px solid #dfdfdd;
        position: relative;
        .company {
          padding-top: 0.2rem;
          padding-left: 0.2rem;
          span {
            font-size: 0.22rem;
            color: #999;
          }
        }
        .btn {
          position: absolute;
          right: 0;
          bottom: 0.1rem;
          width: 1.5rem;
          height: 0.5rem;
          border: 1px solid #ff881e;
          background: #fff;
          border-radius: 20px;
          color: #ff881e;
        }
        img {
          width: 1.46rem;
          height: 1.46rem;
        }
        .words {
          width: 5.24rem;
          .text {
            height: 0.8rem;
            padding: 0 0.2rem;
            font-size: 0.32rem;
            color: #333;
            line-height: 0.45rem;
            overflow: hidden;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
          }
        }
      }
      .commod {
        padding: 0 0.2rem;
        height: 0.9rem;
        line-height: 0.9rem;
        border-bottom: 1px solid #dfdfdd;
        position: relative;
        span {
          font-size: 0.3rem;
          color: #333;
        }
        .actual {
          padding-left: 0.7rem;
          span {
            color: #ff881e;
          }
        }
        .icon {
          position: absolute;
          right: 0.35rem;
          top: 50%;
          width: 0.38rem;
          height: 0.47rem;
          background: url(../../assets/trash.png) no-repeat;
          background-size: 100% 100%;
          margin-top: -0.235rem;
        }
      }
      .operation {
        height: 0.7rem;
        padding: 0.1rem 0.2rem;
        line-height: 0.7rem;
        .timer {
          font-size: 0.25rem;
          color: #757575;
        }
        .btn {
          width: 1.4rem;
          margin-left: 0.1rem;
          height: 0.7rem;
          background: #fff;
          border: 1px solid #f9781e;
          font-size: 0.27rem;
          color: #f9781e;
          border-radius: 5px;
          outline: none;
        }
      }
    }
  }
}
</style>


